import { DirectiveBinding, VNode } from 'vue'
import * as core from './core'
import { CustomInputElement } from './phone-input.type'
import defaultOptions from './phone-input.type'

export default {
    beforeMount: (el: CustomInputElement, { value, modifiers }: DirectiveBinding, vnode: VNode) => {
        el = core.getInputElement(el)
        const options = Object.assign(core.cloneDeep(defaultOptions), value, modifiers)
        el.options = options
        core.updateValue(el, vnode, { force: options.prefill, blur: true })
    },

    mounted: (el: CustomInputElement) => {
        el = core.getInputElement(el)
        const handlerOwner = el.parentElement || el

        const oninput = (e: Event) => {
            if (e.target !== el) return
            core.inputHandler(e as core.CustomInputEvent)
        }

        handlerOwner.addEventListener('input', oninput, true)

        el.onblur = (e) => core.blurHandler(e)
        el.onfocus = (e) => core.focusHandler(e)

        el.onkeydown = (e) => {
            if(e.key === 'Enter') core.blurHandler(e)
            else core.keydownHandler(e, el)
        }

        el.cleanup = () => handlerOwner.removeEventListener('input', oninput, true)
    },

    updated: (el: CustomInputElement, { value, oldValue, modifiers }: DirectiveBinding, vnode: VNode) => {
        el = core.getInputElement(el)
        const options = el.options
        el.options = Object.assign(options, value, modifiers)

        if (value !== oldValue) {
            core.updateValue(el, vnode, { force: true, blur: true })
        } else {
            core.updateValue(el, vnode)
        }
    },

    unmounted: (el: CustomInputElement) => {
        core.getInputElement(el).cleanup()
    }
}