export type Input = number | string

export interface Options {
    modelValue : string
    separator: string
    prefill : boolean
    prefix: string
}

export interface CustomInputElement extends HTMLInputElement {
    options: Options
    formatValue?: Input
    unformatValue?: Input
    oldValue?: Input
    cleanup: () => void
}

export interface InputCustomStyle {
    style: {
        [key: string]: string;
    }
}

export default {
    modelValue : '',
    separator: '',
    prefill : true,
    prefix: ''
}