import { Options, Input } from './phone-input.type'

export default class PhoneFormat {
    options: Options
    input: Input
    numberRegExp: RegExp

    constructor(config: Options) {
        this.options = Object.assign(config)
        this.input = ''        
        this.numberRegExp = new RegExp('[^0-9]+', 'gi')
    }

    isNull() {
        return !this.input?.toString().replace(this.numberRegExp, '')
    }

    numbers() {
        if (typeof this.input === 'number') {
            return this.input.toString().replace('-', '')
        } else {
            return this.input?.toString().replace(this.numberRegExp, '')
        }
    }

    addFormatPrefix0() {
        let inputValue = this.numbers()
        if(inputValue.length > 10) {
            inputValue = inputValue.substring(0, 10)
        }
        const { separator } = this.options
        if(inputValue.length < 10) {
            return inputValue.replace(/[^\dA-Z]/g, '').replace(/(.{3})/g, `$1${separator}`).trim();
        } else {
            return inputValue.replace(/[^\dA-Z]/g, '').replace(/(.{3})(.{3})(.{4})/g, `$1${separator}$2${separator}$3${separator}`).trim();
        }
    }

    addFormatPrefix855() {
        let inputValue = this.numbers()
        if(inputValue.length > 10) {
            inputValue = inputValue.substring(0, 12)
        }
        const { separator } = this.options
        return inputValue.replace(/[^\dA-Z]/g, '').replace(/(.{3})/g, `$1${separator}`).trim();
    }

    format(input: Input): string {
        this.input = input
        if (this.isNull()) return "";
        
        if (this.options.prefix === '855') {
            if (String(this.numbers()).slice(0, 3) === '855') {
                return this.addFormatPrefix855();
            } else {
                return this.options.prefix + this.addFormatPrefix855();
            }
        } else {
            if (String(this.numbers()).slice(0, 1) === '0') {
                return this.addFormatPrefix0()
            } else {
                return this.options.prefix + this.addFormatPrefix0()
            }
        }
    }

    unformat(input: Input): string {
        this.input = input
        if (this.isNull()) return ""
        return this.numbers()
    }
}