
import { defineComponent } from 'vue'
import { CustomInputEvent } from './lib/core'
import directive from './lib/directive'
import { Input } from './lib/phone-input.type'

export default defineComponent({
    name: 'PhoneInput',
    directives: {
        phoneinput: directive
    },
    props: {
        modelValue: {
            type: [String, Number],
            required: true
        },
        placeholder: {
            type: String,
            default: ''
        },
        prefill: {
            type: Boolean,
            default: true
        },
        prefix: {
            type: String,
            default: ''
        },
        separator: {
            type: String,
            default: ''
        },
        disabled: {
            type: Boolean,
            default: false
        },
        clearInput: {
            type: Boolean,
            default: true
        },
        invalid: {
            type: Boolean,
            default : false
        }
    },
    emits: ['update:modelValue', 'input:modelValue', 'change', 'input', 'onBlur', 'onFocus'],
    data() {
        const formatValue = this.modelValue as Input | undefined
        const unformatValue = '' as Input | undefined
        const isClear = false
        return {
            formatValue,
            unformatValue,
            isClear,
        }
    },
    watch: {
        modelValue(newValue, oldValue) {
            if (newValue !== oldValue) {
                this.formatValue = newValue
            }
        }
    },
    computed: {
        config() {
            const separator = this.separator === '' ? ' ' : this.separator
            const prefix = this.prefix === '' ? '0' : this.prefix
            
            return {...this.$props, separator, prefix}
        },
    },
    methods: {
        input(event: Event) {
            this.isClear = false
            const { target } = event as CustomInputEvent
            this.formatValue = target.formatValue
            this.unformatValue = target.unformatValue
            this.$emit('update:modelValue', this.unformatValue)
            this.$emit('input', event)
        },
        change(event: Event) {
            this.$emit('update:modelValue', this.unformatValue)
            this.$emit('change', event)
        },
        onClearValue() {
            this.$emit('update:modelValue', "")
        },

        onBlur(event: Event) {
            this.$emit('onBlur', event)
        },

        onFocus(event: Event) {
            this.$emit('onFocus', event)
        }
    }
})
